.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ffffff;
  z-index: 1000000;
}

.l-preloader {
  width: 60px;
  height: 60px;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}

.c-preloader {
  width: 60px;
  height: 60px;
  position: relative;
}

.c-preloader::before {
  content: '';
  width: 60px;
  height: 60px;
  display: inline-block;
  position: absolute;
  border-radius: 50%;
  background-color: #000;
  opacity: 0;
  transform: scale(0);
  animation-name: preloading;
  animation-duration: 1.2s;
  animation-delay: 0s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.c-preloader::after {
  content: '';
  width: 60px;
  height: 60px;
  display: inline-block;
  position: absolute;
  border-radius: 50%;
  background-color: #000;
  opacity: 0;
  transform: scale(0);
  animation-name: preloading;
  animation-duration: 1.2s;
  animation-delay: -0.6s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes preloading {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}

